import olSource from 'ol/source/Source';

import { SubjectStatus } from '@igo2/utils';
import {
  DataSourceOptions,
  DataSourceLegendOptions
} from './datasource.interface';

import { DataService } from './data.service';
import { generateIdFromSourceOptions } from '../../utils/id-generator';

import { NetworkService } from '@igo2/core';
import { MVTDataSource } from './mvt-datasource';
import { ViewChild } from '@angular/core';

export abstract class DataSource {
  // @ViewChild(MVTDataSource) child: MVTDataSource;

  public id: string;
  public ol: olSource;

  constructor(
    public options: DataSourceOptions = {},
    protected networkService?: NetworkService,
    protected dataService?: DataService
  ) {
    this.options = options;
    this.id = this.generateId();
    this.ol = this.createOlSource();
  }

  protected abstract createOlSource(): olSource;

  protected generateId(): string {
    return generateIdFromSourceOptions(this.options);
  }

  getLegend(scale?: number): DataSourceLegendOptions[] {
    return this.options.legend ? [this.options.legend] : [];
  }

  onLayerStatusChange(status: SubjectStatus): void {}
}
